<template>
  <Modal
    v-model="isShow"
    draggable
    title="通话中"
    :loading="true"
    @on-ok="close"
    @on-cancel="close"
    ok-text="结束通话"
    cancel-text="">
    <Row>
      <Col span="24">
        <div id="streams">
        </div>
      </Col>
    </Row>
    <Row>
      <Col>
        视频开关 <i-switch v-model="openVideo" @on-change="onVideoOpened">
        <span slot="open">开</span>
        <span slot="close">关</span>
      </i-switch>
      </Col>
    </Row>
    <Row>
      <Col>
        音频开关 <i-switch v-model="openAudio" @on-change="onAudioOpened">
        <span slot="open">开</span>
        <span slot="close">关</span>
      </i-switch>
      </Col>
    </Row>
  </Modal>
</template>

<script>
import random from "../utils/Random";
export default {
  name: "DevicesCheck",
  props: {
    close: Function,
    show: Boolean
  },
  data() {
    return {
      isShow: false,
      openVideo: false,
      openAudio: false,
      remoteStreams: [],
      videoTags: [],
      fullScreen: false
    }
  },
  watch: {
    show(val) {
      this.isShow = val;
      if (this.isShow) {
        this.onVideoCall();
      } else {
        this.onVideoCallDone();
      }
    }
  },
  mounted() {
  },
  methods: {
    onVideoCall: function () {
      let that = this;
      console.log("onVideoCall");
      that.openVideo = that.$store.state.hasVideo;
      that.openAudio = that.$store.state.hasAudio;
      // const roomId = random(100000, 999999);
      const roomId = 88888888;
      that.$store.state.client.join(roomId, function (type, stream) {
        let streamId = stream.getId();
        let streams = document.getElementById('streams');
        console.log("事件类型", type, streamId);
        switch (type) {
          case 'local':
            console.log("add local stream");
            that.addStream(streams, stream);
            that.$store.state.client.publish();
            break;
          case 'subscribed':
            console.log(`add remote stream: ${streamId}`);
            that.addStream(streams, stream);
            break;
          case 'removed':
            console.log(`remove remote stream: ${streamId}`);
            let remoteStreamId = document.getElementById(`stream-${streamId}`);
            remoteStreamId.remove();
            break;
          default:
            break;
        }
      });
    },
    addStream: function (parent, stream) {
      let that = this;
      let streamId = stream.getId();
      let htmlDivElement = document.createElement("div");
      htmlDivElement.id = "stream-" + streamId;
      htmlDivElement.style.width = "200px";
      htmlDivElement.style.height = "200px";
      htmlDivElement.style.display = "inline-block";
      htmlDivElement.style.margin = "5px";
      htmlDivElement.classList.add("video-view");

      let fullElement = document.createElement("i");
      fullElement.classList.add("video-view", "ivu-icon", "ivu-icon-ios-expand");
      fullElement.style.position = "relative";
      fullElement.style.float = "right";
      fullElement.style.width = "20px";
      fullElement.style.height = "20px";
      fullElement.style.top = "25px";
      fullElement.style.right = "5px";
      fullElement.style.zIndex = "111";
      fullElement.style.color = "#fff";

      fullElement.addEventListener('click', function () {
        that.onFullScreen(htmlDivElement, streamId);
      });

      htmlDivElement.appendChild(fullElement);

      parent.appendChild(htmlDivElement);
      stream.play(`stream-${streamId}`);
      that.remoteStreams[streamId] = stream;
      that.videoTags[streamId] = htmlDivElement;
    },
    onFullScreen: function (element, streamId) {
      if (this.fullScreen) {
        this.exitFullscreen();
      } else {
        this.launchFullscreen(element);
      }
      this.fullScreen = !this.fullScreen;
    },
    launchFullscreen: function (element) {
      if(element.requestFullscreen) {
        element.requestFullscreen();
      } else if(element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if(element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
      } else if(element.msRequestFullscreen) {
        element.msRequestFullscreen();
      }
    },
    exitFullscreen: function () {
      if(document.exitFullscreen) {
        document.exitFullscreen();
      } else if(document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if(document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
      }
    },
    onVideoCallDone: function () {
      let streams = document.getElementById('streams');
      streams.innerHTML = "";
      this.$store.state.client.leave();
    },
    onVideoOpened: function () {
      if (this.openVideo) {
        this.$store.state.client.unMuteVideo()
      } else {
        this.$store.state.client.muteVideo()
      }
    },
    onAudioOpened: function () {
      if (this.openAudio) {
        this.$store.state.client.unMuteAudio()
      } else {
        this.$store.state.client.muteAudio()
      }
    }
  }
}
</script>

<style scoped>
#video-call {
  margin: 0 auto;
  padding: 0;
  text-align: center;
  background-color: #33333333;
}
.video-view>div{
  width: 100px !important;
  height: 100px !important;
  display: inline-block !important;
}
#local-stream{
}
#remote-stream{
}
</style>



