<template>
  <div class="container">
      <!-- <TheMatrix/> -->
    <!-- <canvas
      v-show="!isSub"
      id="text"
      :width="canvasWidth"
      :height="canvasHeight"
      style="border: 0px solid #d3d3d3;"
    ></canvas> -->
    <div class="canvas" v-show="!isSub">
      <Row>
        <Col span="6" align="center">
          <img :class="scrollY>=defaultScrollY?'thumb-view':'logo'" src="../assets/wavin.png" />
        </Col>
        <Col span="12">
          <span class="welcome-text">{{show}}</span>
        </Col>
        <Col span="6">
          <div class="contact">
            <Button @click="onContactMe">联系我</Button>
          </div>
        </Col>
      </Row>
    </div>
    <div id="wavin" :class="scrollY>=defaultScrollY?'header':''" v-show="!isSub">
      <ul>
        <li class="menus"><a href="/">我的主页</a></li>
        <li class="menus" v-for="item in pages">
          <a href="javascript:void(0)" @click="onUrlClick(item)">{{
            item.title
          }}</a>
        </li>
        <li
          class="wechat"
          id="wechat"
          @mouseenter="onWechatEnterEvent($event, 'wechat')"
          @mouseleave="onWechatLeaveEvent($event, 'wechat')"
        >
          微信
          <div v-show="onWechat" class="wechat-img">
            <img src="../assets/wechat.jpg" />
          </div>
        </li>

        <li
          class="wechat"
          id="wechatcn"
          @mouseenter="onWechatEnterEvent($event, 'wechat-cn')"
          @mouseleave="onWechatLeaveEvent($event, 'wechat-cn')"
        >
          微信公众号
          <div v-show="onWechatCN" class="wechat-img">
            <img src="../assets/qr_code_wavin.jpg" />
          </div>
        </li>

         <li
          class="wechat"
          id="wechatmp"
          @mouseenter="onWechatEnterEvent($event, 'wechat-mp')"
          @mouseleave="onWechatLeaveEvent($event, 'wechat-mp')"
        >
          微信小程序
          <div v-show="onWechatMp" class="wechat-img">
            <img src="../assets/gh_9f3b8c7d11cd_258.jpg" />
          </div>
        </li>
      </ul>
    </div>
    <div v-show="isSub">
      <Container :url="currentUrl" :onback="onBackClick" />
    </div>
    <hr class="divider"/>
    <div v-show="!isSub">
      <div class="github">
        <div class="item" v-for="item in githubTop">
           <Row>
              <Col span="4"><img width="60" height="60" :src="item.owner.avatar_url"/></Col>
              <Col span="20">
                <Row>
                  <Col span="24">
                    <a :href="item.html_url"> {{item.full_name}} </a>
                  </Col>
                </Row>
                <Row>
                  <Col span="24">
                    {{item.description}}
                  </Col>
                </Row>
              </Col>
          </Row>
        </div>
      </div>
    </div>
    <DevicesCheck v-show="isCheck" :show="isCheck" :close="onDevicesChecked"/>
    <VideoCall v-show="isVideo" :show="isVideo" :close="onVideoDone"/>
  </div>
</template>

<script>
import Container from "./Container";
import Heart from "../utils/Heart";
import random from "../utils/Random";
import VideoCall from "./VideoCall";
import DevicesCheck from "./DevicesCheck";
import RtcClient from "../trtc/rtc-client";

// import TheMatrix from './TheMatrix';
export default {
  components: {
    DevicesCheck,
    Container,
    VideoCall,
    // TheMatrix
  },
  name: "Index",
  data() {
    return {
      msg: "Sorry! this page is not opened yet!, please visit: ",
      show: "Welcome to Wavin Don's Creative Center!",
      pages: [
        {
          title: "我的Github",
          url: "https://github.com/Tangwego",
          frame: false,
        },
        {
          title: "我的Gitee",
          url: "https://gitee.com/Tangwego",
          frame: false,
        },
        {
          title: "我的CSDN",
          url: "https://blog.csdn.net/qq_35699215",
          frame: true,
        }
      ],
      colors: [
        // "#9966ff",
        // "#6666ff",
        // "#6699ff",
        // "#9966ff",
        // "#6666ff",
        // "#6699ff",
        // "#9966ff",
        // "#6666ff",
        // "#6699ff",
        // "#9966ff",
        // "#6666ff",
        // "#2E364F"
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA"
      ],
      onWechat: false,
      onWechatMp: false,
      onWechatCN: false,
      isSub: false,
      currentUrl: "",
      canvasWidth: window.innerWidth - 480,
      canvasHeight: 200,
      heart: null,
      canvas: null,
      githubTop: [],
      scrollY: 0,
      defaultScrollY: 100,
      isCheck: false,
      isVideo: false
    };
  },
  mounted() {
    let that = this;
    this.$Loading.start();
    // that.onInitialCanvas();
    // window.addEventListener('resize', function() {
    //   that.onResize();
    // });
    window.addEventListener('scroll', that.onScrollEvent);
    that.requestGithubApi();
    that.initTRTCClient();
    // that.$Loading.finish();
  },
  beforeCreate() {
    document.title = this.$route.meta.title;
  },
  methods: {
    onInitialCanvas: function() {
      this.canvas = document.getElementById("text");
      let ctx = this.canvas.getContext("2d");

      ctx.font = "40px '隶书'";
      // 创建渐变
      let gradient = ctx.createLinearGradient(0, 0, this.canvas.width, 0);
      let size = this.colors.length;
      for (let i = 0; i < size; i++) {
        gradient.addColorStop(i / size, this.colors[i]);
      }
      // 用渐变填色
      ctx.fillStyle = gradient;
      ctx.fillText(this.show, 40, 100);

      this.heart = new Heart(ctx, 40, 30, 2.5, "red");
      this.heart.draw();
    },
    onResize: function() {
      let ctx = this.canvas.getContext("2d");
      ctx.drawImage(this.canvas, 0, 0);
    },
    requestGithubApi: function(){
      let that = this
      let java = 'https://api.github.com/search/repositories?q=language:java&sort=stars'
      let me = 'https://api.github.com/users/Tangwego/repos'
      this.$http.get(java).then(function(res) {
        that.githubTop = res.body.items
        that.$Loading.finish();
      })
    },
    initTRTCClient: function () {
      const uid = random(1000,9999);
      let userId = uid.toString();
      console.log(`userId: ${userId}`);
      this.$store.state.client = new RtcClient(userId);
    },
    onWechatEnterEvent: function (e, s) {
      if (s == "wechat") {
        if (!this.onWechat) {
          this.onWechat = true;
          this.onWechatMp = false;
          this.onWechatCN = false;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      } else if (s == 'wechat-mp') {
        if (!this.onWechatMp) {
          this.onWechat = false;
          this.onWechatMp = true;
          this.onWechatCN = false;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      } else {
        if (!this.onWechatCN) {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = true;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      }
    },
    onWechatLeaveEvent: function (e, s) {
      if (s == "wechat") {
        if (!this.onWechat) {
          this.onWechat = true;
          this.onWechatMp = false;
          this.onWechatCN = false;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      } else if (s == "wechat-mp") {
        if (!this.onWechatMp) {
          this.onWechat = false;
          this.onWechatMp = true;
          this.onWechatCN = false;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      } else {
          if (!this.onWechatCN) {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = true;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      }
    },
    onUrlClick: function (item) {
      if (item.frame) {
        this.currentUrl = item.url;
        this.isSub = true;
      } else {
        window.open(item.url);
        // window.location.href = item.url;
      }
    },
    onBackClick: function () {
      window.location.href = "/";
    },
    onScrollEvent: function(e) {
      this.scrollY = window.scrollY;
    },
    onContactMe: function (e) {
      // this.isCheck = true;
      this.isVideo = true;
      this.$store.state.hasVideo = this.isVideo;
    },
    onDevicesChecked: function () {
      let that = this;
      that.$store.state.client.leave(function() {
        that.isVideo = that.$store.state.hasVideo;
      });
      that.isCheck = false;
    },
    onVideoDone: function () {
      this.isVideo = false;
    }
  },
};
</script>

<style scoped>
.container {
  width: auto;
  height: auto;
}

.canvas {
  text-align: left;
  height: 100px;
  background-color: #333;
}

.welcome-text {
  height: 100px;
  line-height: 100px;
  font-size: 36px;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
}

.contact {
  text-align: center;
  line-height: 100px;
  width: 100%;
  height: 100%;
  color: #fff;
}

#text {
  padding-top: 50px;
  /* background-color: #000; */
}

#wavin {
  /* background-color: #fff; */
  height: 40px;
  font-family: "Axure Handwriting";
  background-color: #333;
  padding-top: 10px;
}

#wavin > ul {
}

#wavin > ul > li {
  display: inline-block;
  padding: 5px 15px;
  border-radius: 10px;
}

#wavin > ul > li > a {
  /* color: #2e364f; */
  /* color: #E25A53; */
   color: #F1E5D6;
}

#wavin > ul > .menus:hover,
#wavin > ul > .menus > a:hover {
  background-color: red;
  color: white;
}

.wechat {
  color: #fff;
}

#wavin > ul > .wechat:hover,
#wavin > ul > .wechat > a:hover {
  color: #13c2c2;
  cursor: pointer;
}

.wechat-img {
  position: absolute;
  width: 200px;
  height: 200px;
}

.divider {
  padding: 20px;
  background-color: #333;
  border: none;
}
.github {
  text-align: left;
  border: 10px solid #333;
}

.github > .item {
  padding: 5px;
  border: 5px solid #333;
}

.header{
  position: fixed;
  top: 0;
  left: 0;
  background-color: #000;
  height: 40px;
  width: 100%;
  z-index: 888888;
}

.thumb-view {
  display: inline-block;
  position: fixed;
  top: 5px;
  left: 10px;
  width: 30px;
  height: 30px;
  z-index: 999999;
}

.logo {
  display: inline;
  margin-top: 10px;
  width: 100px;
  height: 100px;
  z-index: 999999;
}
</style>



