//生成从minNum到maxNum的随机数
function random(minNum, maxNum) {
    let result = 0;
    switch (arguments.length) {
        case 1:
            result = parseInt(Math.random() * minNum + 1, 10);
            break;
        case 2:
            result = parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
            break;
        default:
            break;
    }
    return result;
}

export default random



