/* global $ TRTC getCameraId getMicrophoneId resetView isHidden shareUserId addMemberView removeView addVideoView setAnimationFrame clearAnimationFrame*/
import TRTC from "trtc-js-sdk";
import genTestUserSig from "../utils/TRTCUtils";
class RtcClient {
  constructor(userId) {
    this.userId_ = userId;
    let config = genTestUserSig(userId);
    this.sdkAppId_ = config.sdkAppId;
    this.userSig_ = config.userSig;

    this.isJoined_ = false;
    this.isPublished_ = false;
    this.localStream_ = null;
    this.remoteStream_ = null;

    // create a client for RtcClient
    this.client_ = TRTC.createClient({
      mode: 'rtc',
      sdkAppId: this.sdkAppId_,
      userId: this.userId_,
      userSig: this.userSig_
    });
  }

  join(roomId, callback) {
    let that = this;
    if (that.isJoined_) {
      console.warn('duplicate RtcClient.join() observed');
      return;
    }
    that.client_.join({roomId}).then(()=> {
      that.localStream_ = TRTC.createStream({userId: this.userId_, audio: true, video: true});
      // 设置视频属性 Profile 为 ‘480p’
      that.localStream_.setVideoProfile('480p');
      that.localStream_.initialize().then(() => {
        console.log('local stream init success');
        that.isJoined_ = true;
        callback('local', that.localStream_);
      });
      that.handleEvents(callback);
    });
  }

  handleEvents(callback) {
    let that = this;
    that.client_.on('stream-added', event => {
      console.log('remote stream init success');
      that.remoteStream_ = event.stream;
      that.client_.subscribe(event.stream);
      callback('added', that.remoteStream_);
    });
    that.client_.on('stream-subscribed', event => {
      callback('subscribed', event.stream);
    });
    that.client_.on('stream-removed', event => {
      callback('removed', event.stream);
      that.remoteStream_.stop();
      that.remoteStream_.close();
      that.remoteStream_ = null;
    });
  }

  publish() {
    let that = this;
    if (that.isPublished_) {
      console.warn('duplicate RtcClient.publish() observed');
      return;
    }
    that.client_.publish(that.localStream_).then(()=>{
      console.log("local stream has been published");
      that.isPublished_ = true;
    })
  }

  leave(callback) {
    let that = this;
    if (!that.isJoined_ || !that.localStream_) {
      console.warn('leave() - please join() firstly');
      return;
    }

    if (that.isPublished_) {
      that.client_.unpublish(that.localStream_).then(() => {
        that.client_.leave().then(() => {
          that.localStream_.stop();
          that.localStream_.close();
          that.localStream_ = null;
          that.isJoined_ = false;
          if (typeof callback === 'function') {
            callback();
          }
        }).catch(err => {
          console.error(`exit room error ${err}`);
        });
        that.isPublished_ = false
      });
    } else {
      that.client_.leave().then(() => {
        that.localStream_.stop();
        that.localStream_.close();
        that.localStream_ = null;
        that.isJoined_ = false;
        if (typeof callback === 'function') {
          callback();
        }
      }).catch(err => {
        console.error(`exit room error ${err}`);
      });
    }
    that.isPublished_ = false;
  }

  muteAudio() {
    let that = this;
    if (!that.isJoined_ || !that.localStream_) {
      console.warn('muteAudio() - please join() firstly');
      return;
    }
    that.localStream_.muteAudio();
  }

  unMuteAudio() {
    let that = this;
    if (!that.isJoined_ || !that.localStream_) {
      console.warn('unmuteAudio() - please join() firstly');
      return;
    }
    that.localStream_.unmuteAudio();
  }

  muteVideo() {
    let that = this;
    if (!that.isJoined_ || !that.localStream_) {
      console.warn('muteVideo() - please join() firstly');
      return;
    }
    that.localStream_.muteVideo();
  }

  unMuteVideo() {
    let that = this;
    if (!that.isJoined_ || !that.localStream_) {
      console.warn('unMuteVideo() - please join() firstly');
      return;
    }
    that.localStream_.unmuteVideo();
  }
}

export default RtcClient



