<template>
  <Modal
    v-model="isShow"
    title="设备检测"
    :loading="true"
    @on-ok="close"
    @on-cancel="close"
    ok-text="完成"
    cancel-text="跳过">
    <Row>
      <Col span="24">
        <div id="test-local-stream"></div>
      </Col>
    </Row>
    <Divider />
    <Row>
      <Col>
        是否启用音频 <i-switch v-model="openAudio" @on-change="onAudioOpened">
        <span slot="open">开</span>
        <span slot="close">关</span>
      </i-switch>
      </Col>
    </Row>

    <Row>
      <Col>
        是否启用视频 <i-switch v-model="openVideo" @on-change="onVideoOpened">
        <span slot="open">开</span>
        <span slot="close">关</span>
      </i-switch>
      </Col>
    </Row>
  </Modal>
</template>

<script>
import RTCDetect from "rtc-detect";

export default {
  name: "DevicesCheck",
  props: {
    close: Function,
    show: Boolean
  },
  data() {
    return {
      isShow: false,
      openVideo: false,
      openAudio: true
    }
  },
  watch: {
    show(val) {
      this.isShow = val;
      if (this.isShow) {
        if (this.openVideo) {
          this.onCheck();
        }
      } else {
        this.onUncheck();
      }
    }
  },
  mounted() {
    this.$store.state.hasAudio = this.openAudio;
  },
  methods: {
    onCheck: function () {
      console.log("onCheck");
      let that = this;
      let detect = new RTCDetect();
      detect.getReportAsync().then(result => {
        console.log('result is: ', result);
        if (!result.APISupported.isWebRTCSupported) {
          that.$Notice.error({
            title: "消息提示",
            desc: "该浏览器不支持WebRTC"
          });
          return;
        }
        that.$store.state.client.join(9999, function (type, stream) {
          stream.play('test-local-stream');
        })
      });
    },
    onUncheck: function () {
      this.$store.state.client.leave();
    },
    onVideoOpened: function () {
      this.$store.state.hasVideo = this.openVideo;
      if (this.openVideo) {
        this.onCheck();
      } else {
        this.onUncheck();
      }
    },
    onAudioOpened: function () {
      this.$store.state.hasAudio = this.openAudio;
    }
  }
}
</script>

<style scoped>
#local-stream{
}
</style>



